import React, { Component } from "react";
import { Field, reduxForm, formValueSelector } from "redux-form";
import { connect } from "react-redux";
import { createNumberMask } from "redux-form-input-masks";
import { getTimbangan } from "../../GetTimbangan";
import { renderField, renderFieldSelect } from "../../renderField";
import { AxiosMasterGet } from "../../Axios";
import { SkeletonLoading } from "../../GlobalComponent/function";
import { setFocus } from "../../helper";
import { warning } from "../../Alert";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});

class FormTerimaSupplier extends Component {
  constructor(props) {
    super(props);
    this.onclick1 = this.onclick1.bind(this);
    this.state = {
      kodeBarang: [],
      DataSupplier: [],
      non_aksesoris: "col-lg-12 row",
      aksesoris: "col-lg-12 row d-none",
      kode_barang: "",
      keterangan: "INI KETERANGAN",
      kadar: "",
      P: "",
      K: "",
      bungkus: "",
      berat: "",
      harga: "",
      murni: "",
      ongkos: "",
      status: "",
      kadarCetak: "",
      kadarBeli: "",
      namaJenis: "",
      kodeJenis: "",
    };
  }

  componentDidMount() {
    AxiosMasterGet("jenis/get/all-no-image")
      .then((res) =>
        this.setState({
          kodeBarang: res.data.filter(
            (elm) =>
              elm.kode_kelompok === "AKSESORIS" ||
              elm.kode_kelompok === "PERHIASAN"
          ),
        })
      )
      .catch((err) => console.log(err));
    getTimbangan(this.props.change);
  }

  onclick1() {
    if (this.state.total_netto < this.props.netto) {
      warning("Berat Melebihi Stock Tersedia");
      return false;
    }
    this.props.change("netto", this.props.netto);
    this.props.change("noFaktur", this.props.noFaktur);
    this.props.change("gross", this.props.gross);
  }

  pilihBarang(data) {
    var supp = data
      ? data
      : "DEFAULT | DEFAULT | DEFAULT | DEFAULT | DEFAULT | DEFAULT";
    var result_explode = supp.split("|");
    if (this.props.from === "return") {
      AxiosMasterGet(
        "jenis/get/stock-by-jenis-and-lokasi/" + result_explode[0]
      ).then((res) => {
        this.setState({
          total_netto: res.data?.total_netto,
        });
        if (res.data?.total_netto < 1) {
          warning("Stock Barang Kurang Dari 1");
          this.props.change("kodeJenis", "");
          this.props.change("kadar", "");
          this.props.change("p", "");
          this.props.change("k", "");
          this.props.change("pkg", "");
          this.props.change("bruto", "");
          this.props.change("kadarTransaksi", "");
          this.props.change("gross", "");
          this.props.change("netto", "");
          this.props.change("kode_kategori", "");
          this.props.change("rp", "");
          this.props.change("qty", "");
          this.props.change("harga_beli", "");
          return false;
        }
        return false;
      });
    }
    if (result_explode[5] === "AKSESORIS") {
      this.setState({
        non_aksesoris: "col-lg-12 row d-none",
        aksesoris: "col-lg-12 row",
      });
    } else {
      this.setState({
        non_aksesoris: "col-lg-12 row",
        aksesoris: "col-lg-12 row d-none",
      });
    }
    this.setState({
      kodeJenis: result_explode[0],
      namaJenis: result_explode[1],
      kadarCetak: result_explode[2],
      kadarBeli: result_explode[3],
      kodeKategori: result_explode[4],
      harga_beli: result_explode[6],
    });
    setFocus("P");
  }

  setKadar() {
    this.props.change("keterangan", this.state.namaJenis);
    this.props.change("kadar", this.state.kadarCetak);
    this.props.change("kadarTransaksi", this.state.kadarBeli);
    this.props.change("kode_kategori", this.state.kodeKategori);
    this.props.change("harga_beli", this.state.harga_beli);
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-12 col-lg-12 d-none">
            <Field
              name="noFaktur"
              label={this.props.noFaktur}
              type="text"
              readOnly={true}
              component={renderField}
              defaultValue={this.props.noFaktur}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-4 form-group">
            {String(this.state.kodeBarang) !== "" ? (
              <Field
                label="Kode Model"
                name="kodeJenis"
                component={renderFieldSelect}
                options={this.state.kodeBarang.map((list) => {
                  let data = {
                    value:
                      list.kode_jenis +
                      "|" +
                      list.nama_jenis +
                      "|" +
                      list.kadar_cetak +
                      "|" +
                      list.kadar_beli +
                      "|" +
                      list.kode_kategori +
                      "|" +
                      list.kode_kelompok +
                      "|" +
                      list?.harga_beli,
                    name: list.nama_jenis,
                  };
                  return data;
                })}
                onChange={(data) => this.pilihBarang(data)}
                onBlur={this.setKadar()}
                placeholder="Silahkan Pilih"
              ></Field>
            ) : (
              <SkeletonLoading label="Kode Model" />
            )}
          </div>
          <div
            className="col-md-8 form-group"
            style={
              this.props.from === "terima" ? null : { visibility: "hidden" }
            }
          >
            <Field
              name="keterangan"
              label="Keterangan"
              type="text"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className={this.state.non_aksesoris}>
            <div className="col-md-3 form-group">
              <Field
                name="kadar"
                label="Cap Kode"
                type="number"
                component={renderField}
                readOnly={true}
              />
            </div>
            <div className="col-md-3 form-group">
              <Field name="p" label="P" type="number" component={renderField} />
            </div>
            <div className="col-md-3 form-group">
              <Field name="k" label="K" type="number" component={renderField} />
            </div>
            <div className="col-md-3 form-group">
              <Field
                name="pkg"
                label="Bungkus (Gr)"
                type="number"
                component={renderField}
                onBlur={this.onclick1()}
              />
            </div>
            <div className="col-md-3 form-group">
              <Field
                name="bruto"
                label="Berat (Gr)"
                type="number"
                component={renderField}
                onBlur={this.onclick1()}
              />
            </div>
            <div className="col-md-3 form-group">
              <Field
                name="kadarTransaksi"
                label="Kode Awal (%)"
                type="number"
                component={renderField}
                onBlur={this.onclick1()}
              />
            </div>
            <div className="col-md-3 form-group">
              <Field
                name="gross"
                label="Gross (Gr)"
                type="number"
                component={renderField}
                readOnly={true}
              />
            </div>
            <div className="col-md-3 form-group">
              <Field
                name="netto"
                label="Murni (Gr)"
                type="number"
                component={renderField}
                readOnly={true}
              />
            </div>
            <div className="col-md-3 form-group d-none">
              <Field
                name="kode_kategori"
                label="Kode Kategori"
                type="number"
                component={renderField}
                readOnly={true}
              />
            </div>
            <div className="col-md-3 form-group">
              <label htmlFor="">Ongkos (Rp)</label>
              <Field
                name="rp"
                component="input"
                type="tel"
                className="form-control"
                {...currencyMask}
              />
            </div>
          </div>
          <div className={this.state.aksesoris}>
            <div className="col-md-4 form-group">
              <Field
                name="qty"
                label="QTY"
                type="number"
                component={renderField}
              />
            </div>
            <div className="col-md-4 form-group">
              <label htmlFor="">Harga Beli (Rp)</label>
              <Field
                name="harga_beli"
                component="input"
                type="tel"
                className="form-control"
                {...currencyMask}
              />
            </div>
          </div>
          <div className="col-md-12 form-group mt-3">
            <div className="text-right">
              <button
                className="btn btn-primary"
                type="submit"
                disabled={this.props.submitting}
              >
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormTerimaSupplier = reduxForm({
  form: "TerimaSupplier",
  enableReinitialize: true,
})(FormTerimaSupplier);
const selector = formValueSelector("TerimaSupplier"); // <-- same as form name

export default connect((state) => {
  const { bruto, kadarTransaksi, pkg } = selector(
    state,
    "bruto",
    "kadarTransaksi",
    "pkg"
  );
  return {
    netto: (Number(bruto) * (Number(kadarTransaksi) / 100)).toFixed(3),
    gross: (Number(bruto) + Number(pkg)).toFixed(2),
    initialValues: {
      discGlobalPr: 0,
      p: 0,
      k: 0,
      pkg: 0,
      bruto: 0,
      rp: 0,
    },
  };
})(FormTerimaSupplier);
